.grid {
  display: grid;
}

.sec1 {
  grid-column: 1/4;
  grid-row: 1/1;
}

.sec2 {
  grid-column: 1/4;
  grid-row: 2/5;
}

.sec3 {
  grid-column: 1/4;
  grid-row: 5/7;
}

.sec4 {
  grid-column: 1/2;
  grid-row: 7/10;
  border: solid green 2px;
}

.sec5 {
  grid-column: 2/3;
  grid-row: 7/10;
  border: solid red 2px;
}

.sec6 {
  grid-column: 3/4;
  grid-row: 7/10;
  border: solid blue 2px;
}

.sec7 {
  grid-column: 1/4;
  grid-row: 10/11;
}
